每名会员分摊的互助金额设置上限。"),_c('br'),_vm._v("\n        3.3. 平台根据相关计划章程计算每名会员的分摊金额，在扣款前进行公示，扣款后发送通知。最终扣款金额会根据会员人数变化的实际情况进行必要的调整。"),_c('br'),_vm._v("\n        3.4. 会员有义务保证个人账户余额充足，以便履行互助义务。"),_c('br'),_vm._v("\n        3.5. 互助分摊计算精确至分位，如产生超出当次互助总额的部分，计为互助结余，用于抵扣下次互助金。"),_c('br'),_vm._v("\n        3.6."),_c('br'),_vm._v("\n        账户余额低于1元将被冻结，不参与互助分摊计算，30天（含）复效期内可通过充值激活，否则自动退出。激活续费需补缴所有冻结期间未参与分摊的互助金总额，补缴金额计入本条款3.5所述互助结余中，激活续费后余额需大于1元。30天复效期内如果发生互助事件，仍可申请互助，但如果复效期未续费激活，复效期后发生的互助事件不予互助。"),_c('br'),_vm._v("\n        3.7.   会员申请退出预缴型计划，或发生其它退出计划条件发生时，不返还以往已分摊的互助资金，仅自缴费部分账户余额可以申请返还，相关手续费由会员自行承担。"),_c('br'),_vm._v("\n        4. 非预缴型计划"),_c('br'),_vm._v("\n        4.1.   非预缴型计划加入之前无需预先缴纳互助金。"),_c('br'),_vm._v("\n        4.2.   对某项非预缴型计划，当会员的申领请求被核准后，其他会员根据个人意愿进行自愿式互助。"),_c('br'),_vm._v("\n        5. 会员的权利与义务"),_c('br'),_vm._v("\n        5.1.   会员可自愿加入、自愿退出相应的互助计划。"),_c('br'),_vm._v("\n        5.2.   会员应依据相关互助计划章程履行施助义务，同时享有对等的受助权利。"),_c('br'),_vm._v("\n        5.3.   会员有权参与平台组织的各项选举、投票、意见征集等其他相关活动。"),_c('br'),_vm._v("\n        5.4.   会员对互助事件必要信息享有知情权，同时有义务保护其他会员的隐私信息，不得外传。"),_c('br'),_vm._v("\n        5.5.   会员有义务保证注册时所提供信息的真实性与有效性，相关信息发生变化时应及时与平台工作人员联系进行变更。"),_c('br'),_vm._v("\n        5.6.   会员在申领互助金时，应根据相关互助计划章程提供相应的信息和资料，并保证真实有效，以供审核。"),_c('br'),_vm._v("\n        5.7.  如会员提供错误或虚假信息，将导致会员资格失效或无法获得互助。"),_c('br'),_vm._v("\n        5.8.   如会员恶意骗取互助金，平台有权代全体会员追偿，并保留进一步追究其法律责任的权利。"),_c('br'),_vm._v("\n        5.9.\n        会员不得利用平台提供的途径（包含但不限于论坛、聊天群等）发布包含诈骗、色情、暴力等违反国家法律法规及政策规定的内容，不得发表含有攻击性、侮辱性的言论，不得捏造事实、造谣传谣，对于情节严重的，平台有权解除其会员资格。"),_c('br'),_vm._v("\n        5.10.\n        平台将根据实际运营需要调整或修改相关规则（包含但不限于公约、章程等），平台认为必要时将征求会员意见，包括但不限于调研、投票等，规则修改进行公示后即视为生效。会员有义务及时接收并了解相关规则变动的信息，如有疑问可以联系平台咨询。"),_c('br'),_vm._v("\n        6. 信息披露机制"),_c('br'),_vm._v("\n        6.1.   本着公正、透明的原则，所有计划的必要信息将通过壁虎互助平台进行及时披露。"),_c('br'),_vm._v("\n        6.2.   信息披露内容包括但不限于：有效会员数量、互助事件审定信息、互助金使用明细、年度计划报告等。"),_c('br'),_vm._v("\n        7.  会员隐私保护策略"),_c('br'),_vm._v("\n        7.1.   平台尽合理义务保护会员隐私资料。"),_c('br'),_vm._v(